import { useRouteLoaderData } from "react-router-dom";

const parseAttribute = id => [null, "", "", "", "", ""][id];
const parseRace = id => [null, "", "", "", "", "", null, null, "", null, ""][id];

const CardInfo = ({cardId}) => {
  const cardData = useRouteLoaderData("root").card;
  return <>({parseAttribute(cardData[cardId]?.attribute) ?? "??"}/{parseRace(cardData[cardId]?.race) ?? "??"}) </>;
};

export default CardInfo;